    color: rgba(127, 252, 127, 0.541);
    margin-bottom: 0;
    margin-top: 0;
    padding-top: 8px;
    padding-bottom: 8px;
    left: 4%; }
  .header__link {
    position: absolute;
    font-size: 2.4em;
    color: rgba(127, 252, 127, 0.541);
    right: 4%;
    margin-bottom: 0;
    margin-top: 0;
    padding-top: 8px;
    padding-bottom: 8px; }
