.main {
  height: calc(100vh - 132px);
  position: relative; }

@media only screen and (max-width: 435px) {
  .main {
    height: calc(100vh - 152px);
    min-height: 400px; } }

.map {
  position: absolute;
  height: 52%;
  width: 55%;
  right: 4%;
  bottom: 5%;
  box-shadow: 2px 2px 4px #0a0a0a;
  border-radius: 9px; }
  .map__content {
    position: absolute;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    z-index: 0;
    border-radius: inherit; }
  .map__curtain {
    position: absolute;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    z-index: 1;
    opacity: 0.6;
    background-color: whitesmoke;
    border-radius: inherit; }
  .map__explanation {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    font-size: 1.4em; }
  .map .invisible {
    display: none; }

@media only screen and (max-width: 900px) {
  .map {
    height: 53.7%; } }

@media only screen and (max-width: 600px) {
  .map {
    width: calc(100% - 4% * 2);
    height: 46%; } }

.brewery-data {
  position: absolute;
  height: 33%;
  width: 55%;
  top: 5%;
  right: 4%;
  background-color: #1e1e1e;
  box-shadow: 2px 2px 4px #0a0a0a;
  color: rgba(127, 252, 127, 0.541);
  text-align: center;
  border-radius: 9px;
  display: flex;
  justify-content: center;
  flex-direction: column; }
  .brewery-data__paragraph {
    font-size: 1.2em;
    padding-left: 10%;
    padding-right: 10%;
    margin: 17px 0; }
  .brewery-data__link {
    display: inline;
    font-size: 1.2em;
    color: rgba(127, 252, 127, 0.541);
    margin: 17px 0; }
    .brewery-data__link:hover {
      color: rgba(127, 252, 127, 0.753); }

@media only screen and (max-width: 900px) {
  .brewery-data {
    height: 34%; } }

@media only screen and (max-width: 600px) {
  .brewery-data {
    width: calc(100% - 4% * 2);
    top: 12%; }
    .brewery-data__paragraph {
      font-size: 1em;
      padding-left: 7%;
      padding-right: 7%;
      margin: 6px 0; }
    .brewery-data__link {
