      margin: 6px 0; } }

.suggestions {
  position: absolute;
  top: 10%;
  left: 4%;
  width: 35%;
  height: 85%;
  box-sizing: border-box;
  border-radius: 9px;
  z-index: 5; }
  .suggestions--invisible {
    display: none; }
  .suggestions__list {
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    list-style-type: none; }
  .suggestions__item {
    height: 6.66%;
    box-shadow: 2px 0px 4px #0a0a0a; }
    .suggestions__item:last-child {
      border-radius: 0 0 9px 9px;
      box-shadow: 2px 2px 4px #0a0a0a; }
    .suggestions__item:first-child {
      box-shadow: 2px 2px 4px #0a0a0a; }
  .suggestions__button {
    color: rgba(127, 252, 127, 0.541);
    background-color: #181818;
    font-family: "Heebo", sans-serif;
    border: none;
    width: 100%;
